<template>
  <b-row class="mt-2">
    <b-col>
      <b-button-group>
        <b-button variant="danger" @click="clearCanvas">
          <font-awesome-icon icon="eraser"></font-awesome-icon>
        </b-button>
        <b-button variant="danger" @click="deleteSelected">
          <font-awesome-icon icon="trash-alt"></font-awesome-icon>
        </b-button>
        <b-form-select
          class="ml-1"
          v-model="selectedPaper"
          :options="options"
          @change="changePaper"
        ></b-form-select>
      </b-button-group>
    </b-col>
  </b-row>
</template>

<script>
export default {
  data() {
    return {
      selectedPaper: 'free-page',
      options: [
        { value: 'free-page', text: ' ' },
        { value: 'dot-paper', text: ' ' },
        { value: 'grid-page', text: ' ' },
        { value: 'harf-line-paper', text: ' ' },
        { value: 'todo-paper', text: '  ' },
      ],
    };
  },
  methods: {
    clearCanvas() {
      if (confirm('  ?')) {
        this.$store.commit('clearCanvas');
      }
    },
    deleteSelected() {
      this.$store.commit('deleteSelected');
    },
    changePaper() {
      this.$store.commit('changePaper', this.selectedPaper);
    },
  },
};
</script>

<style></style>
